.App {
	transition: all 0.5s;
	color: #fff;
	background-color: #16213e;
}

.link-text-decoration {
	text-decoration: none;
	color: #fff;
}

.link-text-decoratio:focus,
.link-text-decoratio:hover,
.link-text-decoratio:visited,
.link-text-decoratio:link,
.link-text-decoratio:active {
	text-decoration: none;
}

table {
	margin-top: 2em;
}
table,
thead,
tbody,
tfoot,
tr,
td,
th {
	text-align: center;
	margin: auto;
	border: 1px solid #dedede;
	padding: 1rem;
}
.table {
	display: table;
}
.tr {
	display: table-row;
}
.thead {
	display: table-header-group;
}
.tbody {
	display: table-row-group;
}
.tfoot {
	display: table-footer-group;
}
.col {
	display: table-column;
}
.colgroup {
	display: table-column-group;
}
.td,
.th {
	display: table-cell;
	width: 50%;
}
.caption {
	display: table-caption;
}

.table,
.thead,
.tbody,
.tfoot,
.tr,
.td,
.th {
	text-align: center;
	margin: auto;
	padding: 1rem;
}
.table {
	background: #fff;
	margin: auto;
	border: none;
	padding: 0;
	margin-bottom: 5rem;
}

.th {
	font-weight: 700;
	border: 1px solid #dedede;
	&:nth-child(odd) {
		border-right: none;
	}
}
.td {
	font-weight: 300;
	// border: 1px solid #dedede;
	border-top: none;
	&:nth-child(odd) {
		border-right: none;
	}
}

.form-input-field {
	input,
	select {
		width: 500px;
		margin: 0.5em 0;
		padding: 0.5em 0.25em;
		border: none;
		border-radius: 0.2em;
		font-size: 1.5em;
		box-shadow: 0 0 0.5em 0.1em rgba(0, 0, 0, 0.2);
	}
}

button {
	padding: 16px 16px;
	margin: 0 8px;
	border: none;
	border-radius: 8px;
	color: #fff;
	font-size: 16px;
	cursor: pointer;
	background-color: #0f3460;
}
button:hover {
	opacity: 0.95;
}

.input-date {
	margin-top: 0.5em;
}
