.admin-dashboard {
	background-color: #16213e;
	height: 100vh;
	color: #fff;
}

.admin-grid {
	display: grid;
	grid-template-columns: 1fr 1fr;
}

.grid-item {
	background-color: #0f3460;
	height: 5em;
	margin: 1em;
	padding: 2em;
	font-size: 1.5em;
	display: flex;
	align-items: center;
	justify-content: center;
}

.grid-item:hover {
	cursor: pointer;
	box-shadow: 0 0 0.7em 0.5em rgba(0, 0, 0, 0.6);
}

.item-alternate {
	background-color: #e94560;
}
